/**
 * http request 
 */
axios.interceptors.request.use(
  (config) => {
    Toast.loading();
    config.data = JSON.stringify(config.data);
    config.headers = {
      "Content-Type": "application/json",
    };
    return config;
  },
  (error) => {
    return Promise.reject(error);
  }
);

/**
 * http response 
 */
axios.interceptors.response.use(
  (response) => {
    Toast.hide();
    if (response.data.errCode === 2) {
      console.log("");
    }
    return response;
  },
  (error) => {
    Toast.hide();
    console.log("", error);
  }
);

/**
 * get
 * @param url  url
 * @param params  
 * @returns {Promise}
 */
export function get(url, params = {}) {
  return new Promise((resolve, reject) => {
    axios.get(`${url}`, {
        params: params,
      }).then((response) => {
        // landing(url, params, response.data);
        if (response && response.data) {
          resolve(response.data);
        } else {
          resolve({});
        }
        
      })
      .catch((error) => {
        reject(error);
      });
  });
}

/**
 * post
 * @param url
 * @param data
 * @returns {Promise}
 */

export function post(url, data) {
  return new Promise((resolve, reject) => {
    axios.post(`${url}`, data).then(
      (response) => {
        if (response) {
          resolve(response.data);
        } else {
          reject(response);
        }
      },
      (err) => {
        reject(err);
      }
    );
  });
}

/**
 * patch
 * @param url
 * @param data
 * @returns {Promise}
 */
export function patch(url, data = {}) {
  return new Promise((resolve, reject) => {
    axios.patch(url, data).then(
      (response) => {
        resolve(response.data);
      },
      (err) => {
        msag(err);
        reject(err);
      }
    );
  });
}

/**
 * put
 * @param url
 * @param data
 * @returns {Promise}
 */

export function put(url, data = {}) {
  return new Promise((resolve, reject) => {
    axios.put(url, data).then(
      (response) => {
        resolve(response.data);
      },
      (err) => {
        msag(err);
        reject(err);
      }
    );
  });
}

